import React from 'react';
import Container from "@material-ui/core/Container";
import { makeStyles } from "@material-ui/core";
import { conetent } from '../../assets/js/globalStyle';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { height, margin, padding } from '@mui/system';

const useStyles = makeStyles((theme) => ({
    conetent: {
        // width: '750px',
        width: '55%'
    },
    
    privacypolicytitle: {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '800',
        fontSize: '20px',
        lineHeight: '24px',
        color: '#CCCCCC',
        marginBottom: '25px'
    },
    privacyparagraphsetup: {
        // width: "350px",
        // height: "385px",
        // margin: '0 auto'
    },
    privacyparagraphtitle: {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '700',
        fontSize: '20px',
        lineHeight: '24px',
        color: '#ADADAD',
        marginBottom: '10px'
    },
    privacycontent: {
        fontFamily: 'Inter',
        fontStyle: 'normal',
        fontWeight: '500',
        fontSize: '18px',
        lineHeight: '22px',
        color: '#ADADAD',
    },
    privacyparagraphsetuptwo: {
        margin: '40px auto',
        // width:"92%"
    },
    paragraphspace: {
        margin: '30px auto'
    }


}));

function PrivacyPage() {
    const classes = useStyles();

    return (<Container maxWidth="lg" className={`${classes.conetent} pp-container`}>
        <main>
            <div>
                <Typography variant="h6" align="center" className={classes.privacypolicytitle}>
                    Privacy Poicy
                </Typography>
            </div>
            <Grid container>
                <Grid item lg={6} md={6} sm={12}>
                    <div className={classes.privacyparagraphsetup} >
                        <Typography variant="h6" className={classes.privacyparagraphtitle}>
                            What is Lorem Ipsum?
                        </Typography>
                        <Typography variant="p" className={classes.privacycontent}>
                            Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s, when an unknown printer took a galley of type and scrambled it to make a type specimen book. It has survived not only five centuries, but also the leap into electronic typesetting, remaining essentially unchanged. It was popularised in the 1960s with the release of Letraset sheets containing Lorem Ipsum passages, and more recently with desktop publishing software like Aldus PageMaker including versions of Lorem Ipsum.
                        </Typography>
                    </div>
                </Grid>

                <Grid item lg={6} md={6} sm={12}>
                    <div className={`${classes.privacyparagraphsetup} mt-atuo`}>
                        <Typography variant="h6" className={classes.privacyparagraphtitle}>
                            Why do we use it?
                        </Typography>
                        <Typography variant="p" className={classes.privacycontent}>
                            It is a long established fact that a reader will be distracted by the readable content of a page when looking at its layout. The point of using Lorem Ipsum is that it has a more-or-less normal distribution of letters, as opposed to using 'Content here, content here', making it look like readable English. Many desktop publishing packages and web page editors now use Lorem Ipsum as their default model text, and a search for 'lorem ipsum' will uncover many web sites still in their infancy. Various versions have evolved over the years, sometimes by accident, sometimes on purpose.
                        </Typography>
                    </div>
                </Grid>
            </Grid>
            <Grid container>
                <Grid item lg={12} md={12} sm={12}>
                    <div className={classes.privacyparagraphsetuptwo}>
                        <Typography variant="h6" className={classes.privacyparagraphtitle}>
                            Where does it come from?
                        </Typography>
                        <Typography variant="p" className={classes.privacycontent}>
                            Contrary to popular belief, Lorem Ipsum is not simply random text. It has roots in a piece of classical Latin literature from 45 BC, making it over 2000 years old. Richard McClintock, a Latin professor at Hampden-Sydney College in Virginia, looked up one of the more obscure Latin words, consectetur, from a Lorem Ipsum passage, and going through the cites of the word in classical literature, discovered the undoubtable source. Lorem Ipsum comes from sections 1.10.32 and 1.10.33 of "de Finibus Bonorum et Malorum" (The Extremes of Good and Evil) by Cicero, written in 45 BC. This book is a treatise on the theory of ethics, very popular during the Renaissance. The first line of Lorem Ipsum, "Lorem ipsum dolor sit amet..", comes from a line in section 1.10.32.
                        </Typography>
                        <div className={classes.paragraphspace}>
                            <Typography variant="p" className={classes.privacycontent}>
                                The standard chunk of Lorem Ipsum used since the 1500s is reproduced below for those interested. Sections 1.10.32 and 1.10.33 from "de Finibus Bonorum et Malorum" by Cicero are also reproduced in their exact original form, accompanied by English versions from the 1914 translation by H. Rackham.
                            </Typography>
                        </div>
                        <Typography variant="h6" className={classes.privacyparagraphtitle}>
                            Where can I get some?
                        </Typography>
                        <Typography variant="p" className={classes.privacycontent}>
                            There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by injected humour, or randomised words which don't look even slightly believable. If you are going to use a passage of Lorem Ipsum, you need to be sure there isn't anything embarrassing hidden in the middle of text. All the Lorem Ipsum generators on the Internet tend to repeat predefined chunks as necessary.
                        </Typography>
                    </div>
                </Grid>
            </Grid>

        </main>
    </Container>
    )
}

export default PrivacyPage;