import React from 'react';
import { Link as RouterLink } from "react-router-dom";
import { Button, Typography } from "@material-ui/core";
import Container from "@material-ui/core/Container";
import Grid from '@material-ui/core/Grid';
import useStyles from "../../assets/js/useStyles";

//Import Images
import amazon from "../../assets/images/amazon.png";
import cardPostFooterImg from "../../assets/images/card-post-footer.png"

//import contant data
import { headersData, imageCardList, WhiteTextTypography } from '../../constantData';

function HomePage() {
    const {
        conetent,
        sectionOne,
        advertisment,
        heading,
        cardComponent,
        socialButton,
        socialRootButtons,
        LatestArticles,
        cardPostDeliverd,
        inputEmail,
        subscribeButton,
        cardPostFooter,
        freeBook,
        advertismentText,
        hrstyle
    } = useStyles();

    const classes = useStyles();

    const getMenuButtons = () => {
        return headersData.map(({ label, href, Icon }) => {
            return (
                <Button
                    startIcon={<Icon />}
                    {...{
                        key: label,
                        color: "inherit",
                        to: href,
                        component: RouterLink,
                        className: `${socialButton} social-button`,
                    }}
                >
                    {label}
                </Button>
            );
        });
    };

    const getCardLists = () => {
        return imageCardList.map(({ image, textPrimary, textSecondary }) => {
            return (
                <div className='card-list'>
                    <div className="image-card">
                        <img src={image} />
                        <div className='image-body'>
                            <p className='text-primary'>{textPrimary}</p>
                            <p className='text-secondary'> {textSecondary}</p>
                        </div>
                    </div>
                </div>
            );
        });
    }
    return (
        <Container maxWidth="lg" className={conetent}>
            <main>
                {/* <div className={advertisment}>
            <strong className={advertismentText}>advertisment</strong>
          </div> */}
                <Grid container className='selection-one'>
                    <Grid item lg={8} sm={12} alignItems={'center'} justifyContent={'space-between'} className='grid-one' >
                        {/* <div className={`${socialRootButtons} social-icon`}>{getMenuButtons()}</div> */}
                        <div className="top-news-card">
                            <img src={amazon} className="amazon-image" />
                            <h2 className={`${heading} sm-font-size`}  >Amazon names consumer CEO as two senior Black leaders depart</h2>
                            <p className='simple-text'>Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s...</p>
                        </div>
                        <div>
                            <Typography variant="h6" align="left" className={`${LatestArticles} text-theme`}>
                                Latest articles
                            </Typography>
                            <hr className={hrstyle} />
                            <div > {getCardLists()}</div>
                        </div>
                    </Grid>
                    <Grid item lg={4} sm={12}>
                        <div className={`${cardComponent} cardComponent`} ></div>
                        <div className={`${cardPostDeliverd} cardPostDeliverd`}>
                            <WhiteTextTypography variant="h6" className="card-post-deliverd-title-1">
                                Get New Posts Delivered to Your Inbox
                            </WhiteTextTypography>
                            <WhiteTextTypography variant="subtitle1" className='card-post-deliverd-title-2'>
                                Subscribe to our mailing list and get interesting stuff and updates to your email inbox.
                            </WhiteTextTypography>
                            <input type="text" placeholder='Email' className={inputEmail} />
                            <Button
                                {...{
                                    color: "inherit",
                                    to: "#",
                                    component: RouterLink,
                                    className: subscribeButton,
                                }}>
                                SUBSCRIBE
                            </Button>
                            <p className={cardPostFooter}>
                                *by submitting your information you agree to the terms of our Privacy Policy. You will receive Weekly Stock Watch email and third party offers. You can unsubscribe anytime by selecting unsubscribe at the bottom of our emails.
                            </p>
                        </div>
                        <img src={cardPostFooterImg} className={freeBook} />
                    </Grid>
                </Grid>
            </main>
        </Container>
    );
}

export default HomePage;
