import React from 'react';
import { Grid, Button, Card, CardContent, Typography, Container, makeStyles } from '@material-ui/core';
import { conetent } from '../../assets/js/globalStyle';

import { alpha, styled } from '@mui/material/styles';
import InputBase from '@mui/material/InputBase';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import TextField, { TextFieldProps } from '@mui/material/TextField';
import FormControl from '@mui/material/FormControl';
import { OutlinedInputProps } from '@mui/material/OutlinedInput';

const useStyles = makeStyles((theme) => ({
    conetent: conetent,
    card: {
        height: "540px",
        width: "475px",
        "background": "rgba(53, 53, 53, 0.19)",
        "boxShadow": "3px 4px 4px rgba(0, 0, 0, 0.69)",
        "backdropFilter": "blur(10px)",
        "margin" : "0 auto"
    }

}));




const BootstrapInput = styled(InputBase)(({ theme }) => ({
    'label + &': {
        marginTop: theme.spacing(3),
    },
    '& .MuiInputBase-input': {
        with: "100%",
        borderRadius: 4,
        position: 'relative',
        backgroundColor: '#2b2b2b',
        border: '1px solid #ced4da',
        fontSize: 16,
        width: 'auto',
        padding: '10px 12px',
        transition: theme.transitions.create([
            'border-color',
            'background-color',
            'box-shadow',
        ]),
        '&:focus': {
            boxShadow: `${alpha(theme.palette.primary.main, 0.25)} 0 0 0 0.2rem`,
            borderColor: theme.palette.primary.main,
        },
    },
}));


function ContactPage() {
    const classes = useStyles();

    return (<Container maxWidth="lg" className={`${classes.conetent} container`}>
        <main>
            <Grid>
                <Card className={`${classes.card}`} >
                    <CardContent>
                        <Typography gutterBottom variant="h5" align="center">
                            Contact Us
                        </Typography>
                        <Typography align="center" variant="body2" color="textSecondary" component="p" gutterBottom>
                            Fill up the form below to send us a message.
                        </Typography>
                        {/* <form>
                            <Grid container spacing={1}>
                                <Grid xs={12} sm={12} item>
                                    <FormControl variant="standard">
                                        <InputLabel shrink htmlFor="bootstrap-input">
                                            Bootstrap
                                        </InputLabel>
                                        <BootstrapInput defaultValue="" id="bootstrap-input" />
                                    </FormControl>
                                </Grid>
                            </Grid>
                        </form> */}
                    </CardContent>
                </Card>
            </Grid>
        </main>
    </Container>
    )
}

export default ContactPage;