import {
  AppBar,
  Toolbar,
  Typography,
  makeStyles,
  Button,
  IconButton,
  Drawer,
  Link,
  MenuItem,
  Container,
  Avatar
} from "@material-ui/core";
import MenuIcon from "@material-ui/icons/Menu";
import React, { useState, useEffect } from "react";
import { Link as RouterLink } from "react-router-dom";
import Applogo from "../../assets/images/logo-small.png";
import lightMode from "../../assets/images/light-mode.png";
import nightMode from "../../assets/images/night-mode.png";
import Applogoligth from "../../assets/images/light/light-logo.png";

import Switch from '@mui/material/Switch';

const headersData = [
  {
    label: "Home",
    href: "/home",
  },
  {
    label: "News",
    href: "/news",
  },
  {
    label: "Stocks",
    href: "/stocks",
  },
  {
    label: "Markets",
    href: "/market",
  },
  {
    label: "Contact",
    href: "/contact",
  },
  {
    label: "About",
    href: "/about",
  },
];

const useStyles = makeStyles(() => ({
  conetent: {
    backgroundColor: "#000000",
    boxShadow: "0px 4px 4px rgb(0 0 0 / 25%)"
  },
  header: {
  },
  logo: {
    borderRight: "1px solid #514F4F",
    fontFamily: "Work Sans, sans-serif",
    fontWeight: 600,
    color: "#FFFEFE",
    textAlign: "left",
    width: "196px",
    height: "80px"
  },
  menuButton: {
    fontFamily: 'Inter',
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: "17px",
    lineHeight: "21px",
    color: "#FFFFFF",
    textTransform: "capitalize",
    marginLeft: "7px",
    marginRight: "7px",
  },
  toolbar: {
    padding: 0,
    display: "flex",
    justifyContent: "space-between",
    height: "89px"
  },
  drawerContainer: {
    padding: "20px 30px",
    background: "#000000",
    color: "#fff"
  },
  lightModeButton: {
    marginLeft: "25px",
    marginRight: "7px",
    color: "#FFFFFF",
    fontSize: "17px",
    lineHeight: "21px",
  },
  menuloginButton: {
    fontFamily: 'Inter',
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: "17px",
    lineHeight: "21px",
    color: "#FFFFFF",
    background: "rgba(188, 188, 188, 0.1)",
    borderRadius: "2px",
    marginLeft: "15px",
    marginRight: "7px",
    textTransform: "capitalize",
  },
  menusignupButton: {
    fontFamily: 'Inter',
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: "17px",
    lineHeight: "21px",
    color: "#FFFFFF",
    background: "#154691",
    borderRadius: "2px",
    textTransform: "capitalize"
  },
  menuloginButtonMobile: {
    fontFamily: 'Inter',
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: "17px",
    lineHeight: "21px",
    color: "#FFFFFF",
    background: "rgba(188, 188, 188, 0.1)",
    borderRadius: "2px",
    textTransform: "capitalize",
    width: "150px",
    marginBottom: "10px !important",
    margin: "0 auto"
  },
  menusignupButtonMobile: {
    fontFamily: 'Inter',
    fontStyle: "normal",
    fontWeight: 800,
    fontSize: "17px",
    lineHeight: "21px",
    color: "#FFFFFF",
    background: "#154691",
    borderRadius: "2px",
    textTransform: "capitalize",
    width: "150px",
    marginBottom: "10px !important",
    margin: "0 auto"
  },
  drawercss: {
    background: "#000000",
    color: "#fff"
  },
}));

export default function Header() {
  const {
    header,
    logo,
    menuButton,
    toolbar,
    drawerContainer,
    conetent,
    menuloginButton,
    menusignupButton,
    menuloginButtonMobile,
    menusignupButtonMobile,
    drawercss,
    lightModeButton
  } = useStyles();

  const [state, setState] = useState({
    mobileView: false,
    drawerOpen: false,
  });

  const { mobileView, drawerOpen } = state;
  const label = { inputProps: { 'aria-label': 'Switch demo' } };

  useEffect(() => {
    const setResponsiveness = () => {
      return window.innerWidth < 900
        ? setState((prevState) => ({ ...prevState, mobileView: true }))
        : setState((prevState) => ({ ...prevState, mobileView: false }));
    };

    setResponsiveness();

    window.addEventListener("resize", () => setResponsiveness());

    return () => {
      window.removeEventListener("resize", () => setResponsiveness());
    };
  }, []);

  const displayDesktop = () => {
    return (
      <Container maxWidth="lg">
        <Toolbar className={toolbar}>
          {Logo}
          <div>
            {getMenuButtons()}

            <Button
              onClick={(e) => {
                var element = document.body;
                element.classList.toggle("light-mode");
              }}
              {...{
                color: "inherit",
                className: `${lightModeButton} light-mode-button`,
                startIcon: < Avatar src={lightMode} />
              }}
            >
            </Button>

            <Button
              onClick={(e) => {
                var element = document.body;
                element.classList.toggle("light-mode");
              }}
              {...{
                color: "inherit",
                className: `${lightModeButton} night-mode-button`,
                startIcon: < Avatar src={nightMode} />
              }}
            >
            </Button>

            <Button
              {...{
                color: "inherit",
                to: "/login",
                component: RouterLink,
                className: `${menuloginButton} nav-bar login-button`,
              }}
            >
              Log in
            </Button>
            <Button
              {...{
                color: "inherit",
                to: "/signup",
                component: RouterLink,
                className: menusignupButton,
              }}
            >
              Sign Up
            </Button>
            {/* <Switch
              {...label}
              defaultChecked
              className="switch-theme"
              onChange={(e) => {
                var element = document.body;
                element.classList.toggle("light-mode");
              }} /> */}
          </div>
        </Toolbar>
      </Container >
    );
  };

  const displayMobile = () => {
    const handleDrawerOpen = () =>
      setState((prevState) => ({ ...prevState, drawerOpen: true }));
    const handleDrawerClose = () =>
      setState((prevState) => ({ ...prevState, drawerOpen: false }));

    return (
      <Toolbar>
        <IconButton
          {...{
            edge: "start",
            color: "inherit",
            "aria-label": "menu",
            "aria-haspopup": "true",
            onClick: handleDrawerOpen,
            className: 'humber'
          }}
        >
          <MenuIcon />
        </IconButton>

        <Drawer
          {...{
            anchor: "left",
            open: drawerOpen,
            onClose: handleDrawerClose
          }}
        >
          <div>{Logo}</div>
          <div className={`${drawerContainer} drawerContainer`}>
            {getDrawerChoices()}
          </div>
          <Button
            {...{
              color: "inherit",
              to: "/login",
              component: RouterLink,
              className: menuloginButtonMobile,
            }}
          >
            Log in
          </Button>
          <Button
            {...{
              color: "inherit",
              to: "/signup",
              component: RouterLink,
              className: menusignupButtonMobile,
            }}
          >
            Sign Up
          </Button>
        </Drawer>

        <div>{Logo}</div>
      </Toolbar>
    );
  };

  const getDrawerChoices = () => {
    return headersData.map(({ label, href }) => {
      return (
        <Link
          {...{
            component: RouterLink,
            to: href,
            color: "inherit",
            style: { textDecoration: "none" },
            key: label,
          }}
        >
          <MenuItem>{label}</MenuItem>
        </Link>
      );
    });
  };

  const Logo = (
    <>
      <img className={`${logo} header-logo-dark`} src={Applogo} />
      <img className={`${logo} header-logo-light`} src={Applogoligth} />
    </>
  );

  const getMenuButtons = () => {
    return headersData.map(({ label, href }) => {
      return (
        <Button

          {...{
            key: label,
            color: "inherit",
            to: href,
            component: RouterLink,
            className: `${menuButton} nav-bar`,
          }}
        >
          {label}
        </Button>
      );
    });
  };

  return (
    <header className={conetent}>
      <AppBar className={header}>
        {mobileView ? displayMobile() : displayDesktop()}
      </AppBar>

    </header>
  );
}
